.shared-styles {
  background-color: #272727;
  box-shadow: 1px 1px 1px 0px rgba(0,0,0,0.33);
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  padding: 20px;
}
.search-bar {
  margin-bottom: 20px;
  width: 100%;
}

.search-bar input {
  background-color: #191919;
  border: 1px solid #383838;
  color: #FFF;
  font-size: 1.1rem;
  height: 30px;
  margin-left: 20px;
  width: 60%;
}
.video-item {
  align-items: center;
  cursor: pointer;
  display: flex;
  margin-bottom: 20px;
}

.video-item:last-child {
  margin-bottom: 0;
}

.video-item img {
  box-shadow: 1px 1px 1px 0px rgba(0,0,0,0.33);
  margin-right: 20px;
  width: 55%;
}
.video-list {
  font-size: 0.85rem;
  height: 80vh;
  overflow-y: auto;
  width: 30%;
}
.video-details {
  box-shadow: 1px 3px 2px 0px rgba(0,0,0,0.33);
  display: flex;
  flex-direction: column;
  height: 80vh;
  margin-right: 20px;
  width: 60%;
}

.video-details iframe {
  border: 1px solid #191919;
  min-height: 75%;
  margin: 0 auto;
  width: 100%;
}
.app {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: auto;
  width: 80vw;
}
